.first-reason {
  display: flex;
  flex-direction: column;
}
.first-reason .first-reason__descrip {
  display: flex;
  width: 320px;
}
.first-reason .first-reason__descrip--blue {
  background-color: #81b3d2;
}
.first-reason .first-reason__descrip-block {
  color: #ffffff;
  width: 282px;
  margin: auto;
  text-align: center;
  padding-top: 54px;
  padding-bottom: 40px;
}
.first-reason .first-reason__title {
  margin-bottom: 11px;
}
.first-reason .first-reason__num {
  margin-bottom: 17px;
}
@media (min-width: 768px) {
  .first-reason {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .first-reason .first-reason__image {
    display: flex;
  }
  .first-reason .first-reason__descrip-block {
    color: #ffffff;
    width: 192px;
    margin: auto;
    text-align: center;
    padding-top: 40px;
    padding-bottom: 44px;
  }
  .first-reason .first-reason__descrip {
    width: 256px;
  }
}
@media (min-width: 1280px) {
  .first-reason {
    display: flex;
    flex-direction: row;
    justify-content: center;
  }
  .first-reason .first-reason__descrip-block {
    color: #ffffff;
    width: 280px;
    margin: auto;
    text-align: center;
    padding: 47px 52px 57px 53px;
  }
  .first-reason .first-reason__descrip {
    width: 400px;
  }
}
